import React from 'react'

import { StyleProp, ViewStyle } from 'react-native'

import { render } from '@testing-library/react-native'
import EStyleSheet from 'react-native-extended-stylesheet'

import { ActionButtons } from './ActionButtons'
const isLiked = false

describe('ActionButtons Component', () => {
  it('renders admin menu button if isAdmin is true', () => {
    const handleAdminMenu = jest.fn()
    const { getByTestId } = render(
      <ActionButtons
        isAdmin={true}
        commCount={5}
        likeCount={10}
        handleAdminMenu={handleAdminMenu}
        handleComment={() => {}}
        handleLike={() => {}}
        handleShareLink={() => {}}
        isLiked={isLiked}
      />,
    )
    const adminMenuButton = getByTestId('admin-menu-button')
    expect(adminMenuButton).toBeTruthy()
  })

  it('renders comment and like buttons', () => {
    const { getByTestId } = render(
      <ActionButtons
        isAdmin={false}
        commCount={5}
        likeCount={10}
        handleAdminMenu={() => {}}
        handleComment={() => {}}
        handleLike={() => {}}
        isLiked={isLiked}
        handleShareLink={() => {}}
      />,
    )
    const commentButton = getByTestId('comment-button')
    const heart = isLiked ? 'heart' : 'heart-outline'
    const likeButton = getByTestId(heart)
    expect(commentButton).toBeTruthy()
    expect(likeButton).toBeTruthy()
  })

  it('applies correct styles to comment and like buttons', () => {
    const { getByTestId } = render(
      <ActionButtons
        isAdmin={false}
        commCount={5}
        likeCount={10}
        handleAdminMenu={() => {}}
        handleComment={() => {}}
        handleLike={() => {}}
        isLiked={isLiked}
        handleShareLink={() => {}}
      />,
    )
    const commentButton = getByTestId('comment-button')
    const heart = isLiked ? 'heart' : 'heart-outline'
    const likeButton = getByTestId(heart)
    const commentButtonStyles = commentButton.props
      .style as StyleProp<ViewStyle>
    const likeButtonStyles = likeButton.props.style as StyleProp<ViewStyle>

    expect(commentButtonStyles).toMatchObject({
      justifyContent: 'flex-start', // Ожидаемый стиль для кнопки комментария
    })

    expect(likeButtonStyles).toMatchObject({
      // ваш ожидаемый стиль для кнопки лайка
    })
  })
})
